import classNames from 'classnames';
import styles from './passport-page.module.scss';
import Header from '../components/header/Header';
import { HashLink } from 'react-router-hash-link';
import Lottie from 'lottie-react';
// import arrowDown from './../assets/animations/arrow-down.json';
import arrowDown from './../assets/animations/arrow-down.lottie';
import passport from './../assets/images/passport-2.jpg';
import Footer from '../components/footer/Footer';
// import worker from './../assets/animations/worker.json';
import worker from './../assets/animations/worker.lottie';
import { DotLottiePlayer } from '@dotlottie/react-player';
import ModalUp from '../components/modal-up/ModalUp';
import bg from './../assets/images/bg-3.png';

const PassportPage = () => {
  return (
    <div className={styles.passportPageWrapper}>
      <ModalUp />
      <div className={styles.bg}>
        <Header />
        <div className={classNames(styles.container, styles.passportPage)}>
          <h2 id='title' data-aos='fade-right' className={styles.title}>
             
          </h2>
          <h3 data-aos='fade-right' className={styles.subtitle}>
              ?
          </h3>
          <HashLink smooth to='#contentContainer' className={styles.arrow}>
            {/* <Lottie animationData={arrowDown} /> */}
            <DotLottiePlayer src={arrowDown} autoplay loop />
          </HashLink>
          <div id='contentContainer' className={styles.contentContainer}>
            <div className={styles.passportContainer}>
              <div className={styles.descriptionContainer}>
                <p data-aos='fade-right' className={styles.description}>
                  <span> </span> -   3  
                  ,       . 
                           , 
                     .
                </p>
                <div data-aos='fade-right' className={styles.priceContainer}>
                      600-1000.
                </div>
              </div>
              <div data-aos='fade-left' className={styles.imageContainer}>
                <img src={passport} alt='Passport' />
              </div>
            </div>
            <div className={styles.additionalFunction}>
              <h3 data-aos='fade-right' className={styles.titleSecond}>
                 ' '
              </h3>
              <h4 data-aos='fade-right' className={styles.subtitleSecond}>
                 :
              </h4>
              <div className={styles.textContainer}>
                <p
                  data-aos='fade-right'
                  className={classNames(styles.additional)}
                >
                  <span> </span>  
                    .   ,  
                     . <br /> <br />
                         ? -  ! 
                  -     ,  
                     ,    
                  .
                </p>
                <div data-aos='zoom-in' className={styles.animationContainer}>
                  {/* <Lottie animationData={worker} /> */}
                  <DotLottiePlayer src={worker} autoplay loop />
                </div>
              </div>
              <h5 data-aos='zoom-in' className={styles.serviceTitle}>
                 
              </h5>
              <div data-aos='fade-right' className={styles.circleContainer}>
                <div className={styles.circle}>1</div>
                <p className={styles.description}>
                  C          
                    ! ,    
                  ,    -   ,    
                     !   ,    
                  (.  15-20 )      
                      !   !  
                  !
                </p>
              </div>
              <div data-aos='fade-right' className={styles.circleContainer}>
                <div className={styles.circle}>2</div>
                <p className={styles.description}>
                   -  ,     ,  
                   ,       
                  ,       
                   .
                </p>
              </div>
              <div data-aos='fade-right' className={styles.circleContainer}>
                <div className={styles.circle}>3</div>
                <p className={styles.description}>
                        (   
                   , , )
                </p>
              </div>
              <div data-aos='fade-right' className={styles.circleContainer}>
                <div className={styles.circle}>4</div>
                <p className={styles.description}>
                   ,   !        !
                </p>
              </div>
            </div>
          </div>
        </div>
        <div className={styles.imgContainerTwo}>
          <img src={bg} alt='block background image' />
        </div>
        <Footer />
      </div>
    </div>
  );
};

export default PassportPage;
