import classnames from 'classnames';
import styles from './advantages.module.scss';
import Lottie from 'lottie-react';
// import city from './../../assets/animations/city.json';
import city from './../../assets/animations/city.lottie';
// import designer from './../../assets/animations/designer.json';
import designer from './../../assets/animations/designer.lottie';
// import review from './../../assets/animations/review.json';
import review from './../../assets/animations/review.lottie';
// import arrowDown from './../../assets/animations/arrow-down.json';
import arrowDown from './../../assets/animations/arrow-down.lottie';
import { Link } from 'react-router-dom';
import { DotLottiePlayer } from '@dotlottie/react-player';
import { HashLink } from 'react-router-hash-link';

const Advantages = () => {
  return (
    <div className={styles.advantagesWrapper}>
      <div className={classnames(styles.container, styles.advantages)}>
        <h2 data-aos='fade-right' className={styles.title}>
           
        </h2>
        <h3 data-aos='fade-right' className={styles.subtitle}>
              ?
        </h3>
        <div className={styles.advantageContainer}>
          <div className={styles.textContainer}>
            <div
              data-aos='fade-down'
              data-aos-duration='1000'
              className={styles.numberContainer}
            >
              <div className={styles.circle}>
                <span className={styles.number}>1</span>
              </div>
              <h4 className={styles.advantageTitle}>
                 \  / 
              </h4>
            </div>
            <p
              data-aos='fade-down'
              data-aos-duration='1000'
              className={styles.text}
            >
               ,  ,     
               -     .   
              ,     !    , 
                  30   
              -!
            </p>
          </div>
          <div
            data-aos-duration='2000'
            data-aos='flip-right'
            className={styles.animationContainer}
          >
            <DotLottiePlayer src={designer} autoplay loop={false} />
          </div>
        </div>
        <div className={styles.advantageContainer}>
          <div
            data-aos-duration='2000'
            data-aos='flip-left'
            className={styles.animationContainer}
          >
            <DotLottiePlayer src={city} autoplay loop />
          </div>
          <div className={styles.textContainer}>
            <div
              data-aos='fade-down'
              data-aos-duration='1000'
              className={styles.numberContainer}
            >
              <div className={styles.circle}>
                <span className={styles.number}>2</span>
              </div>
              <h4 className={styles.advantageTitle}>
                    
              </h4>
            </div>
            <p
              data-aos='fade-down'
              data-aos-duration='1000'
              className={styles.text}
            >
                  -.    
                    ,   .
               /  / 
            </p>
          </div>
        </div>
        <div className={styles.advantageContainer}>
          <div className={styles.textContainer}>
            <div
              data-aos='fade-down'
              data-aos-duration='1000'
              className={styles.numberContainer}
            >
              <div className={styles.circle}>
                <span className={styles.number}>3</span>
              </div>
              <h4 className={styles.advantageTitle}>
                  10 !   !
              </h4>
            </div>
            <p
              data-aos='fade-down'
              data-aos-duration='1000'
              className={styles.text}
            >
                  ,    !
                      !
            </p>
          </div>
          <div
            data-aos-duration='2000'
            data-aos='flip-right'
            className={styles.animationContainer}
          >
            <DotLottiePlayer src={review} autoplay loop />
          </div>
        </div>
        <HashLink smooth to='#steps' className={styles.arrow}>
          <DotLottiePlayer src={arrowDown} autoplay loop />
        </HashLink>
      </div>
    </div>
  );
};

export default Advantages;
